.main-content {
  display: flex;
  flex-direction: row;
  justify-content: space-around;

}

.cardItem-content {
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
  margin: 20px 10px;
}

.card-content-footer {
  display: flex;
  flex-direction: row;
}



@media screen and (max-width: 810px) {
  .card-content-footer {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }

  .header-content{
    display: flex;
    flex-direction: column;
  }

  .main-content {
    display: flex;
    flex-direction: column;
  }
}
