.content-page-header {
  width: 55%;
  padding: 10px 100px;
  color: black;
  text-align: center;
  text-shadow: 2px 2px 4px #0000;
  text-transform: uppercase;
}
.header {
  display: flex;
  flex-direction: column;
  height: auto;
  align-items: center;
  justify-content: center;
}
.header-header-tag {
  font-size: 40px;
  font-family: Arial, Helvetica, sans-serif;
  letter-spacing: 0.2rem;
  text-transform: capitalize;

}

.img-header {
  width: 15%;
  height: auto;
  margin: 50px 0px;

}
.img-header:hover{
  transform: scale(1.4); /* Results in 40 pixels square */
}

.h2-tagg {
  font-family: Arial, Helvetica, sans-serif;
  letter-spacing: 0.2rem;
  margin-top: 40px;
  text-transform: capitalize;
}

.header-button {
  width: fit-content;
  margin-top: 40px;
  padding: 4px 17px;
  border-radius: 10px;
  background-color: transparent;
  text-shadow: 0.012rem 0.012rem #000000;
  border: none;
  font-size: 19px;
  text-transform: capitalize;
  letter-spacing: 0.1rem;
}

img .header-button a{
  text-decoration: none;
  color: inherit;
}


@media screen and (max-width: 770px) {

  .header-button {
    display: none;
  }
  .header-header-tag{
    font-size: large;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
    
 
  }

  .img-header{
    display: block;
    margin-top: 100px;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
  }




}


@media screen and (max-width: 553px) {
  .h2-tagg {
    display: none;
  }

  .header-button {
    display: none;
  }



}