.cardItem-content{
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 60%;
  height: auto;
}
.div-img-main {
  width: 200px;
  height: 150px;
  margin: 20px auto;
}
img {
  width: 100%;
  height: 100%;
}

.img-icon {
  display: flex;
  flex-direction: row;
  padding: 0px 10px;
  margin: 10px;
  align-items: center;
  justify-content: center;
}

.img-icon p {
  padding: 0px 10px;
  margin: 0px 10px;
  text-transform: uppercase;
}

.main-button {
  margin: 20px 0px;
  padding: 10px;
  border-radius: 12px;
  font-family: Arial, Helvetica, sans-serif;
  text-transform: capitalize;
  font-size: large;
  font-weight: 700;
  background-color: #09aeae;
  border: none;
}
.main-button a{
  text-decoration: none;
  color:#ffffff;
  text-transform: uppercase;

}


.main-button:active {
  box-shadow: 0 5px  #666;
  transform: translateY(4px);
}

.frontend{
  display: flex;
  flex-direction: row;
  margin-bottom: 20px;
}
.backend{
  display: flex;
  flex-direction: row;
}


@media screen and (max-width: 800px) {
  .h2-tagg {
    display: none;
  }

  .header-button {
    display: none;
  }

}
